.box{
    width: 400px;
    height: 650px;
    /* border: 1px solid red; */
    margin: auto;
}
.inputBox{
    position: relative;
    width: 310px;
    margin-bottom: 20px;
    margin-left: 10px;
}
.inputBox input{
    width: 100%;
    padding: 15px;
    border: 1px solid black;
    border-radius: 5px;
    outline: none;
}
.inputBox span{
    position: absolute;
    top: 0;
    padding: 10px;
    font-size: 20px;    
    transition: 0.5s;
}
.inputBox input:valid ~span,
.inputBox input:focus ~span
{
    color: blueviolet;
    font-weight: 500;
    transform: translateX(10px) translateY(-7px);
    font-size: 15px;
    padding: 0 10px;
    background: white;
}
.btn{
  width: 85%;
  margin-top: -20px;
  font-size: 15px;
}